<template>
  <section class="pc-container pb-4">
    <div class="pcoded-content">
      <div class="row mb-4">
        <div class="d-flex">
          <h5 v-if="responsable !== null" class="text-dark fw-bold d-flex align-items-center"><button class="btn btn-sm mr-2 btn-primary"
              @click="goBack">
              <i class="material-icons-two-tone text-white">arrow_back</i>
            </button>Expedientes asignados a: {{ responsable.nat_nombres }}
            {{ responsable.nat_apellido_paterno }}
            {{ responsable.nat_apellido_materno }}
          </h5>
        </div>
      </div>
      <!--Panel-->
      <div class="row">
        <div class="col-xl-12 col-md-12">
          <div class="card flat-card">
            <div class="row-table">
              <div class="col-sm-6 card-body br">
                <div class="row">
                  <div class="col-sm-4">
                    <i class="material-icons-two-tone text-primary mb-1">computer</i>
                  </div>
                  <div class="col-sm-8">
                    <h5>{{ trades.length }}</h5>
                    <span>EXPEDIENTES ASIGNADOS</span>
                  </div>
                </div>
              </div>
              <div class="col-sm-6 card-body br">
                <div class="row">
                  <div class="col-sm-4">
                    <i class="material-icons-two-tone text-primary mb-1">text_snippet</i>
                  </div>
                  <div class="col-sm-8">
                    <h5>{{ totalPend }}</h5>
                    <span>EXPEDIENTE PENDIENTES</span>
                  </div>
                </div>
              </div>
              <div class="col-sm-6 card-body br">
                <div class="row">
                  <div class="col-sm-4">
                    <i class="material-icons-two-tone text-primary mb-1">text_snippet</i>
                  </div>
                  <div class="col-sm-8">
                    <h5>{{ totalAtend }}</h5>
                    <span>DOCUMENTOS ATENDIDOS</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card shadow">
            <div class="card-body">
              <div class="table-responsive">
                <table class="table" id="table" ref="myDataTable">
                  <thead>
                    <tr>
                      <th>N° de expediente</th>
                      <th>Área de origen</th>
                      <th>Fecha</th>
                      <th>Referencias</th>
                      <th>Oficio</th>
                      <th>Informe</th>
                      <th>Estado</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="trade in trades" :key="trade.tra_id">
                      <td>
                        <router-link :to="{ name: 'oficio/detalle', params: { id: trade.tra_id } }">
                          {{ trade.tra_number }}
                        </router-link>
                      </td>
                      <td>{{ trade.area.are_name }}</td>
                      <td>{{ formatDate(trade.tra_arrival_date)}}</td>
                      <td>{{ referencias(trade.tra_exp_ext, trade.tra_doc_recep)}} </td>
                      <td>{{ trade && trade.report && trade.report.rep_oficio ? trade.report.rep_oficio : '---' }}</td>
                      <td>{{trade && trade.report && trade.report.rep_informe ? trade.report.rep_informe : '---'}}</td>
                      <td v-if="trade.tra_state_law === 'P'" class="pr-5">
                        <span :class="badgeClassOficio(trade.tra_state_law)">
                          Pendiente
                        </span>
                      </td>
                      <td v-if="trade.tra_state_law === 'F'" class="pr-5">
                        <span :class="badgeClassOficio(trade.tra_state_law)">
                          Terminado
                        </span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { badgeClassOficio } from '@/utils/utils.js';
import feather from 'feather-icons';
import moment from 'moment';
import DataTable from '@/utils/dataTable';
import { badgeClass } from '@/utils/utils.js';
export default {
  name: 'AsignadosAbogado',
  props: ['abo_id'],
  mixins: [DataTable],
  mounted() {
    this.$nextTick(() => {
      feather.replace();
    });
    this.cargarTrades();
  },

  data() {
    return {
      trades: [],
      responsable: null,
      totalPend: 0,
      totalAtend: 0,
      dataTable: null,
      exp: {},
    };
  },

  methods: {
    async cargarTrades() {
      try {
        this.$showLoadingAlert();
        var result;
        var formData;
        formData = {
          abo_id: this.abo_id,
        }
        result = await this.$postData('lawyer/listTrades', this, formData);
        this.trades = result.data;
        const tradesFin = this.trades.filter(trade => trade.tra_state_law === 'F');
        this.totalAtend = tradesFin.length;
        const tradesPend = this.trades.filter(trade => trade.tra_state_law === 'P');
        this.totalPend = tradesPend.length;
        this.responsable = result.lawyer;
        await this.$waitForDataTableDraw(this, 'myDataTable');
        this.$closeLoadingAlert();
      } catch (error) {
        this.$handleApiError(error, 'cargar oficios asignados');
      }
    },
    
    referencias(tra_exp_ext, tra_doc_recep){
      var referencia = "";
      if(tra_exp_ext !== null & tra_doc_recep !== null){
        referencia = tra_exp_ext + ' / ' + tra_doc_recep;
      }else if (tra_exp_ext !== null & tra_doc_recep === null){
        referencia = tra_exp_ext;
      }else if (tra_exp_ext === null & tra_doc_recep !== null){
        referencia = tra_doc_recep;
      }else{
        referencia = '---'
      }
      return referencia;
    },

    formatDate(date) {
      moment.locale('es');
      return moment(date).format('DD MMM YYYY');
    },

    goBack() {
      this.$router.go(-1);
    },
    badgeClassOficio,
  },
};
</script>

<style scoped>
@import '@/assets/css/fileStatus.css';
@import '@/assets/css/dataTable.css';

.container-div {
  position: relative;
}

.content-events {
  position: absolute;
  bottom: 5em;
  right: 5em;
  background-color: #fff;
  padding: 10px;
  border: 1px solid #ccc;
  z-index: 1;
}

.bg-links {
  background: #e3e1fc;
  color: #7267EF;
  border: 1px solid #e3e1fc;
  margin: 2.5px !important;
}
</style>